import Artyom from '/node_modules/artyom.js/build/artyom.js';

export default class ArtyomAssistant {
    constructor() {
        this.artyom = new Artyom();
        this.isListening = false;
        this.sidebarController = null; // Reference to sidebar controller

        // Comprehensive voice commands
        this.setupCommands();
    }

    setupCommands() {
        this.artyom.addCommands([
            {
                indexes: ["text to speech", "start reading"],
                action: () => {
                    console.log("Activating Text-to-Speech");
                    this.sendCommand('tts');
                }
            },
            {
                indexes: ["stop reading", "pause"],
                action: () => {
                    console.log("Stopping Text-to-Speech");
                    this.sendCommand('tts'); // Assuming toggle functionality
                }
            },
            {
                indexes: ["next section", "go next"],
                action: () => {
                    console.log("Moving to Next Section");
                    this.sendCommand('skipToNext');
                }
            },
            {
                indexes: ["previous section", "go back"],
                action: () => {
                    console.log("Moving to Previous Section");
                    this.sendCommand('skipToPrevious');
                }
            },
            {
                indexes: ["open link", "access link"],
                action: () => {
                    console.log("Accessing Current Link");
                    this.sendCommand('accessLink');
                }
            },
            {
                indexes: ["sign language", "show sign language"],
                action: () => {
                    console.log("Activating Sign Language");
                    this.sendCommand('signLanguage');
                }
            },
            {
                indexes: ["image caption", "describe image"],
                action: () => {
                    console.log("Activating Image Caption");
                    this.sendCommand('imageCaption');
                }
            }
        ]);
    }

    // Method to send commands to active tab
    sendCommand(buttonType) {
        if (this.sidebarController) {
            switch(buttonType) {
                case 'tts':
                    this.sidebarController.handleTTS();
                    break;
                case 'skipToNext':
                    this.sidebarController.sendMessageToActiveTab({ action: "skipToNext" });
                    break;
                case 'skipToPrevious':
                    this.sidebarController.sendMessageToActiveTab({ action: "skipToPrevious" });
                    break;
                case 'accessLink':
                    this.sidebarController.sendMessageToActiveTab({ action: "accessLink" });
                    break;
                case 'signLanguage':
                    this.sidebarController.handleSignLanguage();
                    break;
                case 'imageCaption':
                    this.sidebarController.handleImageCaption();
                    break;
            }
        }
    }

    // Enhanced toggle listening method
    toggleListening(sidebarController) {
        this.sidebarController = sidebarController;
        
        if (!this.isListening) {
            this.artyom.fatality(); // Reset previous instance
            this.artyom.initialize({
                lang: "en-US", // Adjust language as needed
                continuous: true,
                listen: true,
                debug: true, // Set to false in production
                speed: 0.8
            }).then(() => {
                console.log("Voice recognition started");
                this.isListening = true;
                
            });
        } else {
            this.artyom.fatality();
            this.isListening = false;
            console.log("Voice recognition stopped");
        }

        return this.isListening;
    }
}
